import React, { useState } from "react";
import "./Header.css";
import { Fade } from "react-reveal";
import { NavLink, Link } from "react-router-dom";
import { greeting, settings } from "../../portfolio.js";
import { CgSun } from "react-icons/cg/";
import { HiMoon } from "react-icons/hi";
import { style } from "glamor";

function Header(props) {
  // get url params
  const url = window.location.href;
  const urlParam = url.split("/");
  const role = urlParam.includes("ds") ? "ds" : "sde";
  const theme = props.theme;

  // const activeStyle = { fontWeight: "bold", backgroundColor: theme.accentColor, color: "#fff" };
  const activeStyle = {};

  const styles = style({
    cursor: "pointer",
    height: "45px",
    width: "45px",
    marginRight: "5px",
    marginLeft: "15px",
    paddingTop: "5px",
    borderRadius: "50%",
    border: "none",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: props.theme.name === "light" ? "#7CD1F7" : "#292C3F",
    outline: "none",
    transition: "all 0.2s ease-in-out",
    ":hover": {
      boxShadow: `0 3px 8px ${
        props.theme.name === "light" ? "#F7D774" : "#646464"
      }`,
    },
  });

  const link = role === "ds" ? "/ds" : "/sde";

  const [currTheme, setCurrTheme] = useState(props.theme);

  function changeTheme() {
    if (currTheme === "light") {
      props.setTheme("dark");
      localStorage.setItem("theme", "dark");
      setCurrTheme("dark");
    } else {
      props.setTheme("light");
      localStorage.setItem("theme", "light");
      setCurrTheme("light");
    }
  }

  const icon =
    props.theme.name === "dark" ? (
      <HiMoon
        strokeWidth={1}
        size={20}
        color={props.theme.name === "light" ? "#F9D784" : "#A7A7A7"}
      />
    ) : (
      <CgSun
        strokeWidth={1}
        size={20}
        color={props.theme.name === "light" ? "#F9D784" : "#A7A7A7"}
      />
    );

  return (
    // <Fade top duration={1000} distance="20px">
    <div>
      <header className="header">
        <NavLink to={link} tag={Link} className="logo">
          <span style={{ color: theme.text }}></span>
          <span className="logo-name" style={{ color: theme.text }}>
            {greeting.logo_name}
          </span>
          <span style={{ color: theme.text }}></span>
        </NavLink>
        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label className="menu-icon" htmlFor="menu-btn">
          <span className="navicon"></span>
        </label>
        <ul className="menu">
          <li>
            <NavLink
              className="homei"
              to={`/${role}/`}
              tag={Link}
              activeStyle={activeStyle}
              style={{ borderRadius: 5, color: theme.text }}
            >
              Home
            </NavLink>
          </li>
          <li>
            <NavLink
              className="ec"
              to={`/${role}/education`}
              tag={Link}
              activeStyle={activeStyle}
              style={{ borderRadius: 5, color: theme.text }}
            >
              Education and Certifications
            </NavLink>
          </li>
          <li>
            <NavLink
              className="xp"
              to={`/${role}/experience`}
              tag={Link}
              activeStyle={activeStyle}
              style={{ borderRadius: 5, color: theme.text }}
            >
              Experience
            </NavLink>
          </li>
          <li>
            <NavLink
              className="projects"
              to={`/${role}/projects`}
              tag={Link}
              activeStyle={activeStyle}
              style={{ borderRadius: 5, color: theme.text }}
            >
              Projects
            </NavLink>
          </li>
          {/* <li>
              <NavLink
                className="cr"
                to={`/${role}/contact`}
                tag={Link}
                activeStyle={{ fontWeight: "bold" }}
                style={{ borderRadius: 5, color: theme.text }}
              >
                Contact and Resume
              </NavLink>
            </li> */}
          {/* <button {...styles} onClick={changeTheme}>
              {icon}
            </button> */}
        </ul>
      </header>
    </div>
    // </Fade>
  );
}

export default Header;
